import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AppComponent } from 'src/app/app.component';
import { PolicyService } from 'src/app/policy.service';


@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  public name: any;
  public aptiscore: any;
  public engscore: any;
  public reascore: any;
  public proscore: any;
  //public id :any= '0'
  constructor(router: Router, public app: AppComponent, private policyService: PolicyService) { }

  ngOnInit() {
    this.app.test_apti = false
    this.app.test_reasoning = false
    this.app.test_programming = false
    this.app.test_verbal = false
    this.app.outside = true
    this.app.testzone = false


    if (this.app.id == undefined) {
      this.app.url = "/login"
      this.app.route()
    }

    this.app.inside = true

    this.policyService.getPolicy(this.app.id).subscribe((data: any) => {
      console.log("here data", data)
      this.aptiscore = +data.scoreapti

      let str = (this.aptiscore / 1) * 2 + "%"
      document.getElementById("apti").style.width = str
      this.engscore = +data.scoreeng
      let str2 = (this.engscore / 1) * 2 + "%"
      document.getElementById("eng").style.width = str2

      this.reascore = +data.scoregk
      let str3 = (this.reascore / 1) * 2 + "%"
      document.getElementById("rea").style.width = str3
      
      this.proscore = +data.scorem
      let str4 = (this.proscore / 1) * 2 + "%"
      document.getElementById("pro").style.width = str4


    })
  }

  starttest(id) {
    this.app.testid = id
    if (id == 1) {
      this.app.test_apti = true;
    }
    if (id == 3) {
      this.app.test_reasoning = true;
    }
    if (id == 2) {
      this.app.test_verbal = true;
    }
    if (id == 4) {
      this.app.test_programming = true;
    }
    this.app.url = "/dashboard/testapti"
    this.app.route()
  }
}
