import { Component, OnInit } from '@angular/core';
import { PolicyService } from 'src/app/policy.service';
import { AppComponent } from 'src/app//app.component';

@Component({
  selector: 'app-log-in',
  templateUrl: './log-in.component.html',
  styleUrls: ['./log-in.component.css']
})
export class LogInComponent implements OnInit {

public username:any;
public password:any;
public message:any;
public messageP=false
  constructor(private policyService: PolicyService,public app:AppComponent) { }
public success:any =false;
  ngOnInit() {
    this.app.outside = false
    this.app.inside=false
    if(this.app.reg==true){
    this.messageP=true

          document.getElementById('jk').setAttribute('class','alert alert-success')      
          this.message = 'Successfully Registered !'+ this.app.name ;
    }
  }

  fuck(){
    console.log('here')
    this.policyService.getPolicies().subscribe((data : any[])=>{
        console.log(data);
      data.forEach(x=>{
        console.log(x)
        if(x.username ==  this.username && x.Password == this.password){
          this.app.name = this.username
          this.app.id = x.id
         this.success=true;
         console.log(this.success)
         this.app.inside = true;
         this.messageP=false
         this.app.url="/dashboard"
         this.app.route()}
      })


      if(this.success == false){
        this.messageP = true
        document.getElementById('jk').setAttribute('class','alert alert-danger')      
        this.message = 'Login Failed! Plese check your credentials'}

    })
  }

}
