import { Component } from '@angular/core';
import { PolicyService } from './policy.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  public inside:any = false;
  public message:any =''
  public url:any='/login'
  public name:any
  public id:any;
  public  testzone= false;
  public testid :any;
  public q1ans :any = 0
  public q2ans :any = 0
  public q3ans :any = 0
  public q4ans :any = 0
  public q5ans :any = 0
  public testscore:any=0
  public outside:any = false
  public unec=false
  public reg= false

  public test_apti = false
  public test_reasoning = false
  public test_verbal = false
  public test_programming = false
  title = 'minimus';

  constructor(private policyService: PolicyService, public router: Router) { }



   ngOnInit() {
     this.policyService.getPolicies().subscribe((data : any[])=>{
         console.log(data);

     })
   }


   route(){
     this.router.navigateByUrl(this.url);
     this.testzone = false
  if(this.unec == true)
     this.unec = false
   }

  cl(){
this.message = "Are you sure that you want to end test ? You score will be changed to 0!"
document.getElementById("msg").setAttribute('class','text-danger')
document.getElementById("btn").click()
this.testscore = 0
this.policyService.getPolicy(this.id).subscribe((data : any)=>{
  if(this.testid ==1){data.scoreapti = this.testscore +""}
  else
  if(this.testid ==2){ data.scoreeng = this.testscore +"";console.log(data.scoreEng)}
else   if(this.testid ==3){data.scoregk = this.testscore +"";}
   else if(this.testid ==4){data.scorem = this.testscore+"";}
   this.unec = true
this.updatePolicy(data);


})
this.url= "/dashboard"

  }

  calculate(){

    this.policyService.getPolicy(this.id).subscribe((data : any)=>{

   if(this.testid ==1){data.scoreapti = this.testscore +""}
 if(this.testid ==2){ data.scoreeng = this.testscore +"";console.log(data.scoreEng)}
  if(this.testid ==3){data.scoregk = this.testscore +"";}
   if(this.testid ==4){data.scorem = this.testscore+"";}

   console.log(data,'checking')

this.updatePolicy(data);


  })

}

public updatePolicy(policy: {id:  number,  username:  string, Password: string, scorem:string,scoregk:string,scoreeng:string,scoreapti:string}){
    this.policyService.updatePolicy(policy).subscribe((ret)=>{
          console.log("Policy updated: ", ret);
          this.url ="/dashboard"
          
          if(this.unec == true){}
          else{this.testzone =  false
          if(this.testscore==50){
          document.getElementById('btn').click()
          this.message = " You have scored explendid!"
          document.getElementById('msg').setAttribute('class','text-success')
          this.url = "/dashboard"}
          else{
            document.getElementById('btn').click()
            this.message = " You are doing good!"
            document.getElementById('msg').setAttribute('class','text-success')
            this.url = "/dashboard"
          }}
         
    });


}






}
