import { Component, OnInit } from '@angular/core';
import { PolicyService } from 'src/app/policy.service';
import { AppComponent } from 'src/app//app.component';
import {Router} from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
public username:any;
public password:any;
public email:any;
public recordlength:any
public message:any;
public messageP=false
router: any;


  constructor(private policyService: PolicyService,public app:AppComponent,router:Router) {
    this.router = router
   }

  ngOnInit() {
    this.app.inside=false

    this.policyService.getPolicies().subscribe((data : any[])=>{
        console.log(data);
        this.recordlength = data.length +1
    })
  }

  fuck(){
let policy:any;
this.createPolicy(policy = { 'id' : this.recordlength,  'username': this.username, 'Password':this.password, 'ScoreM':0,'ScoreGK':0,'scoreEng':0,'scoreApti':0})


  }

  public createPolicy(policy){
    this.policyService.createPolicy(policy).subscribe((ret)=>{
          console.log("Policy created: ", ret);
          this.app.reg = true;
          this.app.name = this.username
          this.router.navigateByUrl('/login');
          this.username=''
          this.password=''
          this.email=''

    });
}


}
